import React, { useState, useContext,} from 'react'
import { TextInput } from 'Components'
import'./style.scss';

const Tagger = ({title, createTag}) => {
	const tagsEG = [{name: 'Tag 1', selected: false},{name: 'Tag 2', selected: false},{name: 'Tag 3', selected: false},{name: 'Tag 4', selected: false},{name: 'Tag 5', selected: false},{name: 'Tag 6', selected: false},{name: 'Tag 7', selected: false},{name: 'Tag 8', selected: false},{name: 'Tag 9', selected: false},{name: 'Tag 10', selected: false}]
	const [search, setSearch] = useState('')
	const [ tags, setTags] = useState([...tagsEG])
	const selected = tags.filter(t => t.selected)
	let unselected = tags.filter(t => !t.selected && t.name.toLowerCase().indexOf(search.toLowerCase()) > -1)
	console.log(unselected.length)

	function selectTag(index){
		let name = unselected[index].name 
		let tagIndex = tags.findIndex((t) => t.name === name)
		let newTags = Object.assign([], tags)
		newTags[tagIndex].selected = true
		setTags(newTags)
	}

	function deselectTag(index){
		let name = selected[index].name 
		let tagIndex = tags.findIndex((t) => t.name === name)
		let newTags = Object.assign([], tags)
		newTags[tagIndex].selected = false
		setTags(newTags)
	}

	function internalCreateTag(){
		let newTag = {name: search, selected: true}
		let newTags = Object.assign([], tags)
		newTags.push(newTag)
		setTags(newTags)
		setSearch('')

	}
	return(
		<div className="tagOuterContainer">
		<p className="title">{title}</p>
		<div className="tagContainer">
			
			<TextInput style={{border:'none', margin:0}}onChange={(e) => setSearch(e.target.value)} value={search}/>
			<div className="bottomContainer">
				{selected.length > 0&&
				<div className="selectedContainer">
				{selected.map((tag, i) => {
					return(
						<div className="selectedTagOuterContainer" key={i} onClick={(e) => {
							e.stopPropagation()
							deselectTag(i)}
						}>
							<p>{tag.name}</p>
							<img src={require('Assets/Icons/closeBlack.svg')} />
						</div>
					)
				})}
				</div>
				}
				<div className="unselectedContainer">
				{unselected.map((tag, i) => {
					
					return(
						<div className="unselectedTagOuterContainer" key={i} onClick={(e) => {
							e.stopPropagation()
							selectTag(i)
						}}>
							<p>{tag.name}</p>
						</div>
					)
				})}
				{unselected.length === 0 && search.length > 0 && createTag &&
					<React.Fragment>
						<p className="title noTagsFound">No tags found</p>
						<div className="unselectedTagOuterContainer"onClick={(e) => {
							e.stopPropagation()
							internalCreateTag()
						}}>
							<p>{`Create tag "${search}"`}</p>
						</div>
					</React.Fragment>
				}
				</div>
			</div>
		</div>
		</div>
	)
}


export default Tagger