import React from 'react'
import './style.scss'

const TextInput = ({onChange, value, placeholder, type, options, style}) => {
	return(
		<div>
		<label>{placeholder}</label>
		{(!type || type === 'textinput') &&
			<input className="textInput" onChange={onChange} value={value} style={style ? style : {}}/>
		}
		{type && type === 'textarea' &&
			<textarea className="textArea" onChange={onChange} value={value} style={style ? style : {}}/>	
		}
		{type && type === 'dropdown' &&
			<select>
				{options && options.map((item, i) => {
					return <option value={item.toLowerCase()}>{item}</option>
				})}
			</select>
		}
		{type && type === 'checkbox' &&
		<React.Fragment>
		
			<input type="checkbox" value={value} />
		</React.Fragment>
		}
		</div>
	)
}

export default TextInput